<template>
    <div id="formio-land-form" class="container container-fluid qg-site-width">
        <div id="qg-three-col" class="row no-secondary-nav">
            <div id="qg-primary-content" role="main">
                <h1>{{ formName }}<br /><em>(Land Act 1994)</em></h1>
                <FormWrapper :projectUrl="projectUrl" :wizardUrl="wizardUrl" :createFormUrl="createFormUrl"
                    :formIdentifier="formIdentifier" :savedApplicationPage="savedApplicationPage"
                    :thankYouPage="thankYouPage" :userId="userId" :submissionId="submissionId" :hasPayment="hasPayment"
                    :paymentUrl="paymentUrl" :recaptchaSiteKey="recaptchaSiteKey" />
                <div class="clearfix"></div>
                <div class="qg-content-footer">
                    <dl>
                        <dt>Version:</dt>
                        <dd>2022/01</dd>
                    </dl>
                </div>
                <div id="qg-options" class="row justify-content-sm-end"></div>
            </div>
            <aside id="qg-secondary-content">
                <RightSideBar />
            </aside>
            <LeftSideBar :formName="formName" />
        </div>
    </div>
</template>

<script>
import { RightSideBar, LeftSideBar } from 'formio-wizard-vite-wrapper';
import FormWrapper from './components/FormWrapper';

export default {
    name: 'App',
    components: {
        LeftSideBar,
        FormWrapper,
        RightSideBar
    },
    data() {
        return {
            // FormIO
            projectUrl: process.env.VUE_APP_PROJECT_URL,
            wizardUrl: process.env.VUE_APP_WIZARD_URL,
            createFormUrl: process.env.VUE_APP_WIZARD_URL,
            formIdentifier: `${process.env.VUE_APP_NAME}-${process.env.VUE_APP_ENV}`,
            formName: `${process.env.VUE_APP_FORM_NAME}`,

            // Matrix page
            savedApplicationPage: process.env.VUE_APP_SAVED_APPLICATION_PAGE,
            thankYouPage: process.env.VUE_APP_THANK_YOU_PAGE,

            // submission
            userId: process.env.VUE_APP_USER_ID,
            submissionId: '',

            // payment
            hasPayment: process.env.VUE_APP_HAS_PAYMENT,
            paymentUrl: process.env.VUE_APP_PAYMENT_URL,
            recaptchaSiteKey: process.env.VUE_APP_RECAPTCHA_SITE_KEY
        }
    },
    beforeMount() {
        let queryString = window.location.search;
        let urlParams = new URLSearchParams(queryString);
        let submissionId = urlParams.get('submissionId')

        this.createFormUrl = this.wizardUrl
        if (submissionId) {
            this.createFormUrl += '/submission/' + submissionId
            this.submissionId = submissionId
        }
    }
}
</script>

